.select-player__back-btn {
  @extend .back-btn;
}

.select-player__add,
.select-player__delete {
  @extend .btn;
  min-width: 350px;
  margin-bottom: 20px;
}

.select-player__delete {
  @extend .btn;
  @extend .btn--red;
  margin-bottom: 30px;
}

.select-player__wrap {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 90px;
}

.select-player__thumb {
  position: relative;
  display: flex;
  flex-direction: column;
}

.select-player__label {
  font-size: 20px;
  margin-bottom: 10px;
  text-shadow: 1px 1px 0 #fff, -1px -1px 0 #fff, 1px -1px 0 #fff,
    -1px 1px 0 #fff;
}

.select-player__select {
  @extend .select-player;
}

.select-player__message {
  @extend .form__message;
}

.select-player__start {
  @extend .btn;
  // @extend .btn--green;
}
